		<div class="energy-market">
			<article class="energy-market__item" v-for="item in items" :key="item.cost">
				<p class="energy-market__item-h">x{{ item.recharges }}</p>
				<button class="energy-market__item-button button button--small button--green">{{ item.cost }}</button>
			</article>
		</div>
	</Modal>
</template>