<script type="text/javascript" lang="ts" setup>

import {reactive, ref, watch} from "vue";

interface ResourcesLoaderProps
{
	images : string[]
}

const emit = defineEmits(['loaded']);
const props = defineProps<ResourcesLoaderProps>();

let loaders = reactive<boolean[]>(
	props.images.map(() => false)
);

watch(() => loaders.reduce((p,t) => p && t), () => {
	emit('loaded');
})

</script>
<template>
	<div class="resources">
		<img
			v-for="(image,i) in props.images"
			:src="image"
			class="resources__item"
			@load="loaders[i] = true"
			@error="loaders[i] = true"
		>
	</div>
</template>